export default {
    addUserCommit (state, data) {
      state.userList.push(data)
      localStorage.setItem('userList', JSON.stringify(state))
    },
    removeUserCommit (state, data) {
      const show = state.userList.find(userList => userList.id === data.id)
      if (show !== undefined) {
        state.userList.splice(state.userList.indexOf(data), 1)
      }
    },
    updateUserCommit (state, data) {
      const record = state.userList.find(userList => userList.id === data.id)
      state.userList[record] = data
    }
  }
  