export default {
  addUserAction (context, payload) {
    return new Promise((resolve, reject) => {
      context.commit('addUserCommit', payload)
      resolve()
    })
  },
  removeUserAction (context, payload) {
    return new Promise((resolve, reject) => {
      context.commit('removeUserCommit', payload)
      resolve()
    })
  },
  updateUserAction (context, payload) {
    return new Promise((resolve, reject) => {
      context.commit('updateUserCommit', payload)
      resolve()
    })
  }
}
